body {
  padding: 0 15px;
  font-family: Tilt Neon, sans-serif;
}

h1 {
  margin-top: 140px;
  margin-left: 40px;
}

.search-form {
  width: 400px;
  background-color: rgba(197, 195, 195, .325);
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: 30px auto 0;
  padding: 30px;
  display: flex;
}

button {
  color: #f0f8ff;
  background-color: #359038;
  border: none;
  border-radius: 40px;
  padding: 14px 40px;
  font-family: Tilt Neon, sans-serif;
  font-size: 16px;
  font-weight: 400;
  transition: all .5s;
  box-shadow: 0 5px 15px rgba(11, 99, 246, .7);
}

button:hover {
  background-color: #43b247;
  transform: translate(-1%, -5%);
  box-shadow: 0 20px 40px rgba(11, 99, 246, .3);
}

input {
  width: 200px;
  text-align: center;
  border: none;
  border-radius: 3px;
  padding: 10px;
  font-family: Tilt Neon, sans-serif;
}

.load-more {
  margin-top: 30px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.load-more:hover, .load-more:focus {
  transform: translate(-51%, -5%);
}

.inputbox {
  border-bottom: 2px solid #fff;
  position: relative;
}

.inputbox label {
  color: #43b247;
  pointer-events: none;
  font-size: 1em;
  transition: all .5s;
  position: absolute;
  top: 19%;
  left: 217px;
}

input:focus ~ label, input:valid ~ label {
  color: rgba(67, 178, 71, .33);
  font-size: 15px;
  top: 19%;
  left: 8%;
}

.inputbox input {
  width: 100%;
  color: #43b247;
  background: none;
  border-bottom: solid;
  border-image: linear-gradient(to right, #43b247, #359038) 1 0%;
  outline: none;
  padding: 0 0 5px;
  font-size: 1em;
}

.gallery {
  width: 100%;
  flex-wrap: wrap;
  gap: 30px;
  margin: 100px auto 0;
  display: flex;
}

.photo-card {
  width: calc(25% - 24.5px);
  border-radius: 5px;
  flex-direction: column;
  display: flex;
  box-shadow: 3px 3px 3px rgba(197, 195, 195, .325);
}

.photo-card-image {
  width: 100%;
  align-self: center;
  margin-top: auto;
}

.info {
  justify-content: space-evenly;
  align-items: flex-end;
  gap: 20px;
  margin-top: auto;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 768px) {
  .photo-card {
    width: calc(50% - 17px);
  }
}

@media screen and (max-width: 375px) {
  .photo-card {
    width: calc(100% - 2px);
  }
}

